<template>
	<fieldset class="form--block">
        <legend class="sub-header checkout--sub-header">Личные данные</legend>
        <div class="form--row checkout--row">
          <div class="checkout--sub-row">
            <div class="checkout--wrap">
              <label for="inn" class="sub2-header checkout--sub2-header">ИНН</label>
              <the-mask @input="changeInnValue($event)"
                        :value="clientData.personalData.inn"
                        id="inn" 
                        mask="##########"
                        pattern="[0-9]{10}" 
                        class="form--input checkout--input" 
                        placeholder="0123456789" 
                        required
                        :disabled="clientData.personalData.inn === null"
                        name="inn"
                        @change.native="setBirthDateFromInn($event.target.value)"
                        oninvalid="setCustomValidity('ИНН должен состоять из 10 цифр')"
                        onchange="try{setCustomValidity('')}catch(e){}">
                </the-mask>
      
                <div>
                  <label for="no-inn" class="form--label-cont checkout--no">
                Нету ИНН
                <input @change="switchInnValue()"
                     id="no-inn"
                     name="no-inn"
                     type="checkbox" 
                     class="form--check form--radio"> 
                <span class="form--checkmark"></span>
                </label>
              </div>
            </div>
          </div>
          <div class="checkout--sub-row">
            <div class="checkout--wrap">
              <label for="surname" class="sub2-header checkout--sub2-header">Фамилия</label>
              <input @input="changeSurnameValue($event.target.value)"
                   :value="clientData.personalData.surname"
                   id="surname" 
                   pattern="^[А-Яа-яіІєЄїЇґҐ]+$" 
                   type="text" 
                   class="form--input checkout--input" 
                   placeholder="Іванов"  
                   required
                   name="surname"
                   oninvalid="setCustomValidity('Фамилия должна состоять из украинских букв')"
                     onchange="try{setCustomValidity('')}catch(e){}">
            </div>
            <div class="checkout--wrap">
              <label for="name" class="sub2-header checkout--sub2-header">Имя</label>
              <input @input="changeNameValue($event.target.value)"
                   :value="clientData.personalData.name"
                   id="name" 
                   pattern="^[А-Яа-яіІєЄїЇґҐ]+$" 
                   type="text" 
                   class="form--input checkout--input" 
                   placeholder="Іван" 
                   required
                   name="name"
                   oninvalid="setCustomValidity('Имя должно состоять из украинских букв')"
                     onchange="try{setCustomValidity('')}catch(e){}">
            </div>
            <div class="checkout--wrap">
              <label for="patronymic" class="sub2-header checkout--sub2-header">Отчество</label>
              <input @input="changePatronymicValue($event.target.value)"
                   :value="clientData.personalData.patronymic"
                   pattern="^[А-Яа-яіІєЄїЇґҐ]+$" 
                   id="patronymic" 
                   type="text" 
                   class="form--input checkout--input" 
                   placeholder="Петрович" 
                   required
                   name="patronymic"
                   oninvalid="setCustomValidity('Отчество должно состоять из украинских букв')"
                     onchange="try{setCustomValidity('')}catch(e){}">
            </div>
          </div>
          <div class="checkout--sub-row">
            <div class="checkout--wrap">
              <label for="date-of-birth" class="sub2-header checkout--sub2-header">Дата рождения</label>
              <input id="date-of-birth" 
                   type="date"
                   min="1900-01-01"
                   :max="maxBirthDate"
                   name="birth-date"
                   @change="changeDateOfBirthValue($event.target.value)"
                   :value="clientData.personalData.dateOfBirth"
                   class="form--input checkout--input checkout--input__date" 
                   required>
            </div>
            
            
            <div class="checkout--wrap">
              <label for="phone" class="sub2-header checkout--sub2-header">Мобильный</label>
              <the-mask 
                   mask="+380 (##) ###-##-##"
                   @input="changePhoneValue($event)"
                   :value="clientData.personalData.phone"
                   pattern="^\+380 \([0-9]{2}\) [0-9]{3}-[0-9]{2}-[0-9]{2}$" 
                   id="phone" 
                   type="tel" 
                   class="form--input checkout--input" 
                   placeholder="+380 (__) ___-__-__" 
                   required
                   name="phone"
                   oninvalid="setCustomValidity('Введите телефон в формате +380 (__) ___-__-__')"
                     onchange="try{setCustomValidity('')}catch(e){}">
                </the-mask>
            </div>
          </div>
        </div>
    </fieldset>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";
  import {TheMask} from 'vue-the-mask';

  export default {
    computed: {
      ...mapGetters([
        "clientData"
        ])
    },

    methods: {
        ...mapActions("personalData", {
            changeInnValue: "changeInnValue",
            switchInnValue: "switchInnValue",
            changeSurnameValue: "changeSurnameValue",
            changeNameValue: "changeNameValue",
            changePatronymicValue: "changePatronymicValue",
            changeDateOfBirthValue: "changeDateOfBirthValue",
            changePhoneValue: "changePhoneValue",
        }),
        maxBirthDate() {
            const date =  new Date;
            date.setFullYear( date.getFullYear() - 18 );
            return date.toISOString().split("T")[0]
        },
        setBirthDateFromInn(e) {
            const daysAmount = +e.substring(0,5);
            const birthDate = new Date(new Date("01.01.1900").getTime() + ((daysAmount)*24*60*60*1000)).toISOString().split("T")[0];
            this.changeDateOfBirthValue(birthDate);
        }
    },

    components: {
        TheMask
    }
  }
</script>





