<template>
	<fieldset class="form--block">
        <template v-if="!transportData.privilege">
          <legend class="sub-header checkout--sub-header">Документ</legend>
      
          <label for="passport" class="checkout--label-cont form--label-cont">
            Пасспорт
            <input :checked="clientData.documents.type === 'passport'"
                 @change="changeTypeValue($event.target.value);clearDocValues()" 
                 id="passport" 
                 type="radio" 
                 name="document" 
                 value="passport" 
                 class="form--check form--radio" 
                 required>
            <span class="form--checkmark checkout--checkmark"></span>
          </label>
      
          <label for="driving-lic" class="checkout--label-cont form--label-cont">
            Права
            <input :checked="clientData.documents.type === 'driving-lic'"
                 @change="changeTypeValue($event.target.value);clearDocValues()" 
                 id="driving-lic" 
                 type="radio" 
                 name="document" 
                 value="driving-lic" 
                 class="form--check form--radio" 
                 required>
            <span class="form--checkmark checkout--checkmark"></span>
          </label>
      
          <label for="id-card" class="checkout--label-cont form--label-cont">
            ID-карта
            <input :checked="clientData.documents.type === 'id-card'"
                 @change="changeTypeValue($event.target.value);clearDocValues()"  
                 id="id-card" 
                 type="radio" 
                 name="document" 
                 value="id-card" 
                 class="form--check form--radio"
                 required>
            <span class="form--checkmark checkout--checkmark"></span>
          </label>
        
          <div class="form--row checkout--row">
            <div v-if="clientData.documents.type === 'passport'" class="checkout--sub-row">
              <div class="checkout--wrap">
                <label for="pasport-lot" class="sub2-header checkout--sub2-header">Серия и номер</label>
                <the-mask 
                     :value="clientData.documents.lot"
                     @input="changeLotValue($event)"
                     mask="FF"
                     :tokens="lotTokens"
                     id="pasport-lot" 
                     type="text" 
                     class="form--input checkout--input checkout--input__lot checkout--input__upper" 
                     placeholder="СВ" 
                     required
                     pattern="^[A-Za-zА-Яа-яіІєЄїЇґҐ]{2}"
                     name="pasport-lot"
                     oninvalid="setCustomValidity('Серия паспорта должна состоять из 2 букв')"
                       onchange="try{setCustomValidity('')}catch(e){}">
                  </the-mask>
                <the-mask 
                     :value="clientData.documents.number"
                     @input="changeNumberValue($event)"
                     mask="######"
                     id="pasport-number" 
                     type="text" 
                     class="form--input checkout--input checkout--input__number" 
                     placeholder="123456" 
                     required
                     name="pasport-number"
                     pattern="[0-9]{6}"
                       oninvalid="setCustomValidity('Номер паспорта должен состоять из 6 цифр')"
                       onchange="try{setCustomValidity('')}catch(e){}">
                  </the-mask>
              </div>
              <div class="checkout--wrap">
                <label for="pasport-issued-by" class="sub2-header checkout--sub2-header">Кем выдан</label>
                <input :value="clientData.documents.issuedBy"
                     @input="changeIssuedByValue($event.target.value)"
                     id="pasport-issued-by" 
                     type="text"
                     name="pasport-issued-by"
                     class="form--input checkout--input checkout--input checkout--input__by" 
                     placeholder="Московським РУ ГУ МВС України в м. Києві" 
                     required
                     pattern="^[А-Яа-яіІєЄїЇґҐ\s_.-]+$"
                     oninvalid="setCustomValidity('Это поле может содержать буквы украинского алфавита, пробелы и точки')"
                       onchange="try{setCustomValidity('')}catch(e){}">
              </div>
              <div class="checkout--wrap">
                <label for="pasport-when" class="sub2-header checkout--sub2-header">Когда выдан</label>
                <input :value="clientData.documents.when"
                     @input="changeWhenValue($event.target.value)"
                     id="pasport-when" 
                     type="date" 
                     name="pasport-when"
                     class="form--input checkout--input checkout--input__date" 
                     required
                     >
              </div>
            </div>
        
            <div v-if="clientData.documents.type === 'driving-lic'" class="checkout--sub-row">
              <div class="checkout--wrap">
                <label for="driving-lic-lot" class="sub2-header checkout--sub2-header">Серия и номер</label>
                <the-mask :value="clientData.documents.lot"
                        @input="changeLotValue($event)"
                        mask="FFF"
                        :tokens="lotTokens"
                        id="driving-lic-lot" 
                        type="text"
                        name="driving-lic-lot"
                        class="form--input checkout--input checkout--input__lot" 
                        placeholder="ВАН" 
                        required
                        pattern="^[A-Za-zА-Яа-яіІєЄїЇґҐ]{3}"
                        oninvalid="setCustomValidity('Серия прав должна состоять из 3 букв')"
                          onchange="try{setCustomValidity('')}catch(e){}">
                  </the-mask>
                <the-mask :value="clientData.documents.number"
                        @input="changeNumberValue($event)"
                        mask="######"
                        id="driving-lic-number" 
                        type="text"
                        name="driving-lic-number"  
                        class="form--input checkout--input checkout--input__number" 
                        placeholder="123456" 
                        required
                        pattern="[0-9]{6}"
                          oninvalid="setCustomValidity('Номер прав должен состоять из 6 цифр')"
                          onchange="try{setCustomValidity('')}catch(e){}">
                  </the-mask>
              </div>
              <div class="checkout--wrap">
                <label for="driving-issued-by" class="sub2-header checkout--sub2-header">Кем выдан</label>
                <input :value="clientData.documents.issuedBy"
                     @input="changeIssuedByValue($event.target.value)"
                     id="driving-issued-by" 
                     type="text"
                     name="driving-issued-by" 
                     class="form--input checkout--input checkout--input checkout--input__by" 
                     placeholder="Центр 0000"
                     required
                     pattern="^[А-Яа-яіІєЄїЇґҐ\s_.-]+$"
                     oninvalid="setCustomValidity('Это поле может содержать буквы украинского алфавита, пробелы и точки')"
                       onchange="try{setCustomValidity('')}catch(e){}">
              </div>
              <div class="checkout--wrap">
                <label for="driving-lic-when" class="sub2-header checkout--sub2-header">Когда выдан</label>
                <input :value="clientData.documents.when"
                     @input="changeWhenValue($event.target.value)"
                     id="driving-lic-when" 
                     type="date" 
                     name="driving-lic-when" 
                     class="form--input checkout--input checkout--input__date" 
                     required>
              </div>
            </div>
      
            <div v-if="clientData.documents.type === 'id-card'" class="checkout--sub-row">
              <div class="checkout--wrap">
                <label for="id-card-number" class="sub2-header checkout--sub2-header">Номер</label>
                <the-mask :value="clientData.documents.number"
                        @input="changeNumberValue($event)"
                        mask="#########"
                        id="id-card-number" 
                        type="text"
                        name="id-card-number"  
                        class="form--input checkout--input checkout--input__number" 
                        placeholder="123456789" 
                        required
                        pattern="[0-9]{9}"
                          oninvalid="setCustomValidity('Номер ID-карты должен состоять из 9 цифр')"
                          onchange="try{setCustomValidity('')}catch(e){}">
                  </the-mask>
              </div>
              <div class="checkout--wrap">
                <label for="id-card-when" class="sub2-header checkout--sub2-header">Действительный до</label>
                <input @input="clientData.idCardWhen = countIdCardDate($event.target.value)" 
                     id="id-card-when" 
                     type="date" 
                     name="id-card-when" 
                     class="form--input checkout--input checkout--input__date" 
                     required>
              </div>
            </div>
          </div>
        </template>
      
        <template v-else>
          <legend class="sub-header checkout--sub-header">{{document.name}}</legend>
          <div class="checkout--sub-row">
            <div class="checkout--wrap">
              <label :for="document.id + '-lot'" class="sub2-header checkout--sub2-header">Серия и номер</label>
              <the-mask :value="clientData.documents.lot"
                    @input="changeLotValue($event)"
                      :mask="document.lotMask"
                      :tokens="lotTokens" 
                      :id="document.id + '-lot'" 
                      type="text"
                      :name="document.id + '-lot'"
                      class="form--input checkout--input checkout--input__lot" 
                      :placeholder="document.lotPlaceholder" 
                      required
                      :pattern="document.lotPattern"
                      :oninvalid='"setCustomValidity(" + document.lotErrorText + ")"'
                        onchange="try{setCustomValidity('')}catch(e){}">
                </the-mask>
              <the-mask :value="clientData.documents.number"
                    @input="changeNumberValue($event)"
                      mask="######"
                      :id="document.id + '-number'"  
                      type="text"
                      :name="document.id + '-number'"  
                      class="form--input checkout--input checkout--input__number" 
                      placeholder="123456" 
                      required
                      pattern="[0-9]{6}"
                      oninvalid="setCustomValidity('Номер документа должен состоять из 6 цифр')"
                        onchange="try{setCustomValidity('')}catch(e){}">
                </the-mask>
            </div>
            <div class="checkout--wrap">
              <label :for="document.id + '-issued-by'" class="sub2-header checkout--sub2-header">Кем выдан</label>
              <input :value="clientData.documents.issuedBy"
                   @input="changeIssuedByValue($event.target.value)"
                   :id="document.id + '-issued-by'" 
                   type="text"
                   :name="document.id + '-issued-by'" 
                   class="form--input checkout--input checkout--input checkout--input__by" 
                   :placeholder="document.issuedBy"
                   required
                   pattern="^[А-Яа-яіІєЄїЇґҐ\s_.-]+$"
                   oninvalid="setCustomValidity('Это поле может содержать буквы украинского алфавита, пробелы и точки')"
                     onchange="try{setCustomValidity('')}catch(e){}">
            </div>
            <div class="checkout--wrap">
              <label :for="document.id + '-issued-when'" class="sub2-header checkout--sub2-header">Когда выдан</label>
              <input :value="clientData.documents.when"
                   @input="changeWhenValue($event.target.value)"
                   :id="document.id + '-issued-when'" 
                   type="date" 
                   :name="document.id + '-issued-when'" 
                   class="form--input checkout--input" 
                   required>
            </div>
          </div>
        </template>
    </fieldset>
</template>

<script>
    import {mapGetters} from "vuex";
    import {mapActions} from "vuex";
    import {TheMask} from 'vue-the-mask';

    export default {
        data() {
            return {
                lotTokens: {
                    F: {
                        pattern: /[a-zа-яёієїґ]+$/i,
                        transform: v => v.toLocaleUpperCase()
                    }
                }
            }
        },

        computed: {
            ...mapGetters([
                "clientData",
                "transportData"
                ]),
            document() {
                switch (this.transportData.privilege) {
                    case "Пенсионер":
                    this.clearDocValues();
                    return {
                        id: "pensioner",
                        name: "Пенсионное удостовирение",
                        lotPlaceholder: "ААЗ",
                        lotMask: "FFF",
                        lotPattern: "^[A-Za-zА-Яа-яіІєЄїЇґҐ]{3}",
                        issuedBy: "Пенсійний фонд України",     
                        lotErrorText: "Серия пенсионного удостовирения должна состоять из 3 букв"
                    } 
                    break;
                    case "Ветеран":
                    this.clearDocValues();
                    return {
                        id: "veteran",
                        name: "Удостоверение ветерана",
                        lotPlaceholder: "ЮА",
                        lotMask: "FF",
                        lotPattern: "^[A-Za-zА-Яа-яіІєЄїЇґҐ]{2}",
                        issuedBy: "Київський обласний військовий комісаріат",
                        lotErrorText: "Серия удостовирения ветерана должна состоять из 2 букв"
                    }
                    break;
                    case "Инвалид второй группы":
                    this.clearDocValues();
                    return {
                        id: "invalid",
                        name: "Удостоверение второй группы инвалидности",
                        lotPlaceholder: "ААБ",
                        lotMask: "FFF",
                        lotPattern: "^[A-Za-zА-Яа-яіІєЄїЇґҐ]{3}",
                        issuedBy: "Управління соціального захисту в м. Києві",
                        lotErrorText: "Серия удостовирения второй группы инвалидности должна состоять из 3 букв"
                    }
                    break;
                    case "Чернобылец первой группы":
                    this.clearDocValues();
                    return {
                        id: "chernobyl",
                        name: "Удостоверение Чернобыльца первой, второй категории",
                        lotPlaceholder: "А",
                        lotMask: "F",
                        lotPattern: "^[A-Za-zА-Яа-яіІєЄїЇґҐ]{1}",
                        issuedBy: "Кабінет Міністрів України",
                        lotErrorText: "Серия удостовирения чернобыльца должна состоять из 1 буквы"
                    }
                    break;
                    default: 
                    return false
                }
            }
        },

        methods: {
            ...mapActions("documents", {
                changeTypeValue: "changeTypeValue",
                changeLotValue: "changeLotValue",
                changeNumberValue: "changeNumberValue",
                changeIssuedByValue: "changeIssuedByValue",
                changeWhenValue: "changeWhenValue"
            }),
            clearDocValues() {
                this.changeLotValue("");
                this.changeNumberValue("");
                this.changeIssuedByValue("");
                this.changeWhenValue("");
            },
            countIdCardDate(e) {
                let dataArr = e.split("-");
                dataArr[0] = +dataArr[0] - 10;
                this.changeWhenValue(dataArr.join("-"));
            }
        },

        mounted() {
            const docType = this.document.id;
            if(docType) {
                this.changeTypeValue(docType)
            }
        },

        components: {
            TheMask
     }
 }
</script>





