<template>
	<fieldset class="form--block">
        <legend class="sub-header checkout--sub-header">Данные о компании</legend>
        <div class="form--row checkout--row">
          <div class="checkout--sub-row">
            <div class="checkout--wrap">
              <label for="egrpou" class="sub2-header checkout--sub2-header">ЕГРПОУ</label>
              <the-mask @input="changeEgrpouValue($event)"
                        :value="clientData.personalData.inn"
                        id="egrpou" 
                        mask="########"
                        pattern="[0-9]{8}" 
                        class="form--input checkout--input" 
                        placeholder="0123456789" 
                        required
                        name="egrpou"
                        oninvalid="setCustomValidity('ЕГРПОУ должен состоять из 8 цифр')"
                        onchange="try{setCustomValidity('')}catch(e){}">
                </the-mask>
      
            </div>
            <div class="checkout--wrap">
              <label for="company-name" class="sub2-header checkout--sub2-header">Тип компании</label>
              <the-mask 
                     :value="companyType"
                     @input="changeCompanyTypeValue($event)"
                     mask="FFFFFF"
                     :tokens="companyTypeTokens"
                     id="company-type" 
                     type="text" 
                     class="form--input checkout--input checkout--input__company-type" 
                     placeholder="ТОВ" 
                     required
                     pattern="^[A-Za-zА-Яа-яіІєЄїЇґҐ]{6}"
                     name="company-type"
                     oninvalid="setCustomValidity('Тип компании состоять максимум из 6 букв')"
                       onchange="try{setCustomValidity('')}catch(e){}">
                  </the-mask>
            </div> 
            <div class="checkout--wrap">
              <label for="company-name" class="sub2-header checkout--sub2-header">Название компании</label>
              <input @input="changeCompanyNameValue($event.target.value)"
                   :value="companyName"
                   id="company-name" 
                   pattern="^[А-Яа-яіІєЄїЇґҐ]+$" 
                   type="text" 
                   class="form--input checkout--input" 
                   placeholder="Страхові послуги"  
                   required
                   name="company-name"
                   oninvalid="setCustomValidity('Название компании должно состоять из украинских букв')"
                    onchange="try{setCustomValidity('')}catch(e){}">
            </div> 
            
            <div class="checkout--wrap">
              <label for="phone" class="sub2-header checkout--sub2-header">Мобильный</label>
              <the-mask 
                   mask="+380 (##) ###-##-##"
                   @input="changePhoneValue($event)"
                   :value="clientData.personalData.phone"
                   pattern="^\+380 \([0-9]{2}\) [0-9]{3}-[0-9]{2}-[0-9]{2}$" 
                   id="phone" 
                   type="tel" 
                   class="form--input checkout--input" 
                   placeholder="+380 (__) ___-__-__" 
                   required
                   name="phone"
                   oninvalid="setCustomValidity('Введите телефон в формате +380 (__) ___-__-__')"
                     onchange="try{setCustomValidity('')}catch(e){}">
                </the-mask>
            </div>
          </div>
        </div>
    </fieldset>
</template>

<script>
  import {mapGetters} from "vuex";
  import {mapActions} from "vuex";
  import {TheMask} from 'vue-the-mask';

  export default {
    data() {
      return {
        companyTypeTokens: {
          F: {
            pattern: /[a-zа-яёієїґ]+$/i
          }
        }
      }
    },
    computed: {
      ...mapGetters([
        "clientData"
        ]),
      ...mapGetters("personalData",{
        companyType: "companyType",
        companyName: "companyName"
      }),
    },

    methods: {
        ...mapActions("personalData", {
            changeInnValue: "changeInnValue",
            switchInnValue: "switchInnValue",
            changeEgrpouValue: "changeEgrpouValue",
            changeSurnameValue: "changeSurnameValue",
            changeNameValue: "changeNameValue",
            changePatronymicValue: "changePatronymicValue",
            changeDateOfBirthValue: "changeDateOfBirthValue",
            changePhoneValue: "changePhoneValue",
            changeCompanyNameValue: "changeCompanyNameValue",
            changeCompanyTypeValue: "changeCompanyTypeValue"
        })
    },

    components: {
        TheMask
    }
  }
</script>





